@import url("https://fonts.googleapis.com/css?family=Marck+Script|Neucha|Russo+One&subset=cyrillic");
@import "reset";
@import "guide";


.hidden {
  display: none; }
.ok {
  display: block; }
.game-field {
  width: 800px;
  position: relative;
  margin: auto;
  cursor: url(/img/cursor.png), auto;
  .level-info {
    margin-top: 30px;
    text-align: center;
    div {
      height: 60px;
      width: 250px;
      .score {
        padding: 5px 40px;
        color: #000;
        background-color: #fff;
        border-radius: 50%;
        vertical-align: center;
        font-family: "Russo One", sans-serif; } } }
  .mob-state {
    p {
      color: goldenrod;
      text-shadow: 1px 1px 1px #000;
      text-transform: uppercase;
      text-align: center;
      position: absolute;
      top: 200px;
      width: 100%;
      border-radius: 30%;
      font-family: "Russo One", sans-serif;
      font-size: 2em; } }
  .actions {
    position: relative;
    margin-bottom: 100px;
    button {
      position: absolute;
      top: 0;
      left: 0;
      background-color: #111;
      color: #fff;
      font-family: "Russo One", sans-serif;
      height: 40px;
      width: 100%;
      text-shadow: 1px 1px 1px #000;
      border: none;
      border-bottom: 2px solid #000; } }
  .game {
    height: 400px;
    .main-character {
      position: relative;
      top: 0;
      height: 350px;
      float: left;
      padding-bottom: 210px;
      img {
        height: 350px;
        width: 350px; } }
    .mob {
      position: relative;
      top: 150px;
      float: right;
      width: 300px;
      padding-bottom: 140px;
      .quote {
        position: absolute;
        top: -50px;
        left: 5px;
        height: 50px;
        width: 200px;
        display: block;
        p {
          color: #000;
          background-color: #fff;
          border-radius: 35%;
          border: 2px solid #000;
          padding: 10px 20px; } }
      img {
        height: 200px; } } }
  .footer {
    text-align: center;
    width: 100%;
    padding: 15px 0;
    background-color: #111;
    color: #fff;
    font-family: "Russo One", sans-serif;
    span {
      font-family: 'Neucha', sans-serif; } } }
